body {
  color: #333;
  z-index: 1;
  background-color: #333;
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: Georgia, serif;
  display: flex;
  position: relative;
}

body:before {
  content: "";
  opacity: .5;
  z-index: -1;
  background-image: url("https://res.cloudinary.com/drkmgpcad/image/upload/v1726809005/o1bv1zzupmhdlccqsfx7.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  position: absolute;
  inset: 0;
}

header {
  color: #fff;
  text-align: center;
  opacity: .9;
  background-color: #333;
  border-bottom: 3px solid #eadca6;
  font-family: Times New Roman, Times, serif;
  font-size: 3rem;
}

nav {
  text-align: center;
  opacity: .9;
  z-index: 1000;
  background-color: #ca965c;
  border-bottom: 2px solid #eadca6;
  padding: 5px;
  position: sticky;
  top: 0;
}

nav button {
  cursor: pointer;
  background-color: #dbcccc;
  border: 1px solid #000;
  border-radius: 25px;
  margin: 0 5px;
  padding: 5px 15px;
}

#category-header {
  text-align: center;
  color: #000;
  padding: 5px;
  font-size: 1.2em;
  font-weight: bold;
}

#articles-container {
  column-count: 3;
  opacity: .95;
  color: #000;
  flex: 1;
  column-gap: 30px;
  padding: 20px;
}

.article {
  text-align: center;
  background-color: #eadca6;
  border: 1px solid #000;
  border-radius: 40px;
  align-self: start;
  margin-bottom: 5%;
  padding: 25px;
  overflow: hidden;
  box-shadow: 0 2px 8px #0003;
}

.article img {
  object-fit: cover;
  border-radius: 20px;
  width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.article h2 {
  margin: 20px;
  font-size: 1.4em;
  line-height: 1.2;
}

.article p {
  color: #000;
  margin: 10px 0;
  font-family: Lucida Sans Unicode, sans-serif;
  font-size: 1em;
  line-height: 1.5;
}

.article a {
  color: #007abf;
  font-size: .9rem;
  font-weight: bold;
  text-decoration: underline;
}

footer {
  color: #fff;
  opacity: .95;
  background-color: #333;
  border-top: 2px solid #eadca6;
  justify-content: start;
  align-items: center;
  padding: 10px 20px;
  display: flex;
}

footer img {
  width: auto;
  height: auto;
}

footer p {
  margin: 0;
  padding-left: 2%;
  font-family: Courier New, sans-serif;
  font-size: 68%;
}

footer p:last-of-type {
  margin-left: auto;
}

#back-to-top {
  color: #fff;
  cursor: pointer;
  z-index: 1000;
  opacity: .7;
  background-color: #007abf;
  border: none;
  border-radius: 50%;
  width: auto;
  height: 12%;
  padding: 0;
  display: none;
  position: fixed;
  bottom: 15%;
  right: 5%;
}

#back-to-top:hover {
  background-color: #005f9e;
}

#back-to-top-image {
  align-self: center;
  width: 100%;
  height: 100%;
}

.loading {
  pointer-events: none;
  opacity: .5;
}

.loading-screen {
  z-index: 9999;
  background-color: #fffc;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.loading-spinner {
  border: 8px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
