body {
    font-family: 'Georgia', serif;
    margin: 0;
    padding: 0;
    background-color: #333; /* Fallback color */
    color: #333;
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 1;
}

/* Overlay the background image with opacity */
body::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url(https://res.cloudinary.com/drkmgpcad/image/upload/v1726809005/o1bv1zzupmhdlccqsfx7.png);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-size: cover;
    opacity: 0.5; /* Change the opacity here */
    z-index: -1; /* Make sure it's behind the content */
}


header {
    background-color: #333;
    color: #ffffff;
    text-align: center;
    font-size: 3rem;
    border-bottom: 3px solid #EADCA6;
    font-family: 'Times New Roman', Times, serif;
    opacity: 0.9; 
}

nav {
    background-color: #CA965C;
    padding: 5px;
    text-align: center;
    border-bottom: 2px solid #EADCA6;
    opacity: 0.9;
    position: sticky;
    top: 0;
    z-index: 1000;
}

nav button {
    background-color: #dbcccc;
    border: 1px solid black;
    padding: 5px 15px;
    margin: 0 5px;
    cursor: pointer;
    border-radius: 25px;
}

#category-header {
    text-align: center;
    padding: 5px;
    font-size: 1.2em;
    font-weight: bold;
    color: black;
}

#articles-container {
    column-count: 3;
    column-gap: 30px;
    padding: 20px;
    flex: 1;
    opacity: 0.95;
    color: black;
}

.article {
    background-color: #EADCA6;
    border: 1px solid black;
    padding: 25px;
    margin-bottom: 5%;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
    border-radius: 40px;
    overflow: hidden;
    text-align: center;
    align-self: start; 
}

.article img {
    width: 100%;
    height: auto;
    object-fit: cover;
    margin-bottom: 10px;
    border-radius: 20px;
}

.article h2 {
    font-size: 1.4em;
    margin: 20px;
    line-height: 1.2;
}

.article p {
    font-family: 'Lucida Sans Unicode', sans-serif;
    font-size: 1em;
    color: black;
    margin: 10px 0;
    line-height: 1.5;
}

.article a {
    color: #007abf;
    font-size: .9rem;
    text-decoration: none;
    font-weight: bold;
    text-decoration: underline;
}

footer {
    background-color: #333;
    color: #fff;
    padding: 10px 20px;
    border-top: 2px solid #EADCA6;
    display: flex;
    align-items: center;
    justify-content: start;
    opacity: 0.95;
}

footer img {
    width: auto;
    height: auto;
}

footer p {
    font-size: 68%;
    font-family: 'Courier New', sans-serif;
    margin: 0;
    padding-left: 2%;
}

footer p:last-of-type {
    margin-left: auto;
}

#back-to-top {
    position: fixed;
    width: auto;
    height: 12%;
    bottom: 15%;
    right: 5%;
    background-color: #007abf;
    color: white;
    border: none;
    padding: 0px;
    border-radius: 50%;
    cursor: pointer;
    display: none;
    z-index: 1000;
    opacity: 0.7;
}

#back-to-top:hover {
    background-color: #005f9e;
}

#back-to-top-image {
    width: 100%;
    height: 100%;
    align-self: center;
}

.loading {
    pointer-events: none;
    opacity: 0.5;
}

.loading-screen {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    display: none;
}

.loading-spinner {
    border: 8px solid #f3f3f3;
    border-top: 8px solid #3498db;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}









